.wrapper {
  display: flex;
  align-content: center;
  align-items: center;
  height: 56px;
}

.pagBox {
  display: flex;
  align-content: center;
  align-items: center;
  width: 360px;
  margin: 0 auto;
  color: #5b5b5b;
  font-size: 14px;
}

.page {
  display: flex;
  align-items: center;
  width: 158px;
}
.title {
  margin-right: 4px;
}
.numPage {
  display: flex;
  align-items: center;
}

.item {
  display: flex;
  width: 100px;
}

.arrowbox {
  display: flex;
  justify-content: space-around;
  width: 72px;
}

.arrowleft {
  display: inline-flex;
  border: none;
  background-color: transparent;
  width: 9px;
  height: 16px;
  background-image: url('../../assets/arrowleft.svg');
  background-repeat: no-repeat;
  cursor: pointer;
  outline: none;
}

.arrowright {
  display: inline-flex;
  border: none;
  background-color: transparent;
  width: 9px;
  height: 16px;
  background-image: url('../../assets/arrowright.svg');
  background-repeat: no-repeat;
  cursor: pointer;
  outline: none;
}
